

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {hrWorkflowDetail, hrworkflowSubmit, hrworkflowRefuse} from '@/api/workflow';
import baseLoading from 'components/base-loading.vue';

export default {
components: {
Group, Cell, Flexbox, FlexboxItem, baseLoading
},
name: 'workflowDetail',
data () {
return {
isLoadingData: true,
status: "",
approveHistoryList: [],
approveHistoryDetails: {},
showDetailHistory: true,
showDetailHistoryOne: true,
showDetailHistoryTwo: true,
hrWorkflowId: ""
}
},
methods: {
refuseButton(){
this.$vux.loading.show({text: '拒绝中'});
hrworkflowRefuse({
"hrWorkflowList": [{
"hrWorkflowId": this.hrWorkflowId,
"hrId": this.hrId
}]
}).then(response => {
if (response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.text("拒绝成功", 'middle');
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
} else {
this.$vux.loading.hide();
}
});
},
agreeButton(){
this.$vux.loading.show({text: '同意中'});
hrworkflowSubmit({
"hrWorkflowList": [{
"hrWorkflowId": this.hrWorkflowId,
"hrId": this.hrId
}]
}).then(response => {
if (response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.text("同意成功", 'middle');
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
} else {
this.$vux.loading.hide();
}
});
}
},
mounted(){
if (this.$route.query.hrWorkflowCheckStatus === '1') {
this.hrWorkflowId = this.$route.query.hrWorkflowId;
this.hrId = this.$route.query.hrId;
this.status = "1";
} else if (this.$route.query.hrWorkflowCheckStatus === '2') {
this.status = "2";
} else if (this.$route.query.hrWorkflowCheckStatus === '3') {
this.status = "3";
}

hrWorkflowDetail().then(response => {
if (response.data.success) {
this.isLoadingData = false;
this.approveHistoryList = response.data.rows[0];
this.approveHistoryDetails = response.data.rows[1][0];
} else {
this.isLoadingData = false;
}
});
},
}



